<template>
  <div ref="wrapper" class="wrapper">
    <slot></slot>
    <slot name="top">
      <div class="pulldownWrapper" :style="pullDownStyle" v-if="pulldown && beforePullDown">
        <bubble :y="bubbleY"></bubble>
      </div>
    </slot>
  </div>
</template>

<script type="text/ecmascript-6">
import BScroll from 'better-scroll'
import Bubble from './bubble.vue'

export default {
  props: {
    // 
    probeType: {
      type: Number,
      default: 1
    },
    // 
    click: {
      type: Boolean,
      default: true
    },
    // 
    data: {
      type: Array,
      default: null
    },
    // 
    pulldown: {
      type: Boolean,
      default: false
    },
    // 
    beforePullDown: {
      type: Boolean,
      default: false
    },
    // 
    listenScroll: {
      type: Boolean,
      default: false
    },
    // 
    pullup: {
      type: Boolean,
      default: false
    },
    //  beforeScroll 
    beforeScroll: {
      type: Boolean,
      default: false
    },
    // 
    refreshDelay: {
      type: Number,
      default: 20
    }
  },
  data() {
    return {
      // 
      bubbleY: 0,
      pullDownStyle: '',
      isPullingDown: false,
      pullDownInitTop: -50,
      isRebounding: false
    }
  },
  mounted() {
    setTimeout(() => {
      this._initScroll()
    }, 20)
  },
  methods: {
    // 
    _initScroll() {
      if (!this.$refs.wrapper) {
        return
      }
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        click: this.click
      })

      // 
      if (this.pulldown) {
        this.scroll.on('touchend', pos => {
          // 
          if (pos.y > 50) {
            this.isRebounding = true
            setTimeout(() => {
              // 
              this.isRebounding = false
            }, 500)
            this.$emit('pulldown')
          }
        })
      }

      // 
      if (this.listenScroll) {
        let me = this

        this.scroll.on('scroll', pos => {
          if (this.beforePullDown) {
            me.bubbleY = Math.max(0, pos.y + this.pullDownInitTop)
            this.pullDownStyle = `top:${Math.min(
              pos.y + this.pullDownInitTop,
              10
            )}px`
          } else {
            me.bubbleY = 0
          }
          if (this.isRebounding) {
            me.pullDownStyle = `top:-50px`
          }

          me.$emit('scroll', pos)
        })
      }

      // 
      if (this.pullup) {
        this.scroll.on('scrollEnd', () => {
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            this.$emit('scrollToEnd')
          }
        })
      }

      //  beforeScroll 
      if (this.beforeScroll) {
        this.scroll.on('beforeScrollStart', () => {
          this.$emit('beforeScroll')
        })
      }
    },
    disable() {
      // better-scrolldisable
      this.scroll && this.scroll.disable()
    },
    enable() {
      // better-scrollenable
      this.scroll && this.scroll.enable()
    },
    refresh() {
      // better-scrollrefresh
      this.scroll && this.scroll.refresh()
    },
    // 
    scrollTo() {
      // better-scrollscrollTo
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    },
    // 
    scrollToElement() {
      // better-scrollscrollToElement
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    }
  },
  watch: {
    data() {
      setTimeout(() => {
        this.refresh()
      }, this.refreshDelay)
    }
  },
  components: {
    Bubble
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.wrapper {
  position: relative;

  .pulldownWrapper {
    position: absolute;
    width: 100%;
    text-align: center;
    top: -50px;
    padding-bottom: 20px;
  }
}
</style>



